(function() {
  var FindDevelopersCtrl,
    bind = function(fn, me){ return function(){ return fn.apply(me, arguments); }; };

  FindDevelopersCtrl = (function() {
    function FindDevelopersCtrl($state, $http, $scope, $uibModal) {
      var newSkills, ref, ref1, ref2, ref3, ref4;
      this.$state = $state;
      this.$http = $http;
      this.$scope = $scope;
      this.$uibModal = $uibModal;
      this.addMarker = bind(this.addMarker, this);
      this.showDeveloperMessage = false;
      this.showDevelopersList = false;
      this.mapLocation = false;
      this.skills = [];
      this.availableSkills = ['JavaScript', 'Java', 'Python', 'CSS', 'PHP', 'Ruby', 'C++', 'C'];
      newSkills = ['Shell', 'C#', 'Objective-C', 'R', 'VimL', 'Go', 'Perl', 'CoffeeScript'];
      (ref = this.availableSkills).push.apply(ref, newSkills);
      newSkills = ['TeX', 'Swift', 'Scala', 'Emacs Lisp', 'Haskell', 'Lua', 'Clojure', 'Matlab', 'Arduino', 'Groovy'];
      (ref1 = this.availableSkills).push.apply(ref1, newSkills);
      newSkills = ['Puppet', 'Rust', 'PowerShell', 'Erlang', 'Visual Basic', 'Processing', 'Assembly', 'TypeScript', 'XSLT'];
      (ref2 = this.availableSkills).push.apply(ref2, newSkills);
      newSkills = ['ActionScript', 'ASP', 'OCaml', 'D', 'Scheme', 'Dart', 'Common Lisp', 'Julia', 'F#', 'Elixir'];
      (ref3 = this.availableSkills).push.apply(ref3, newSkills);
      newSkills = ['FORTRAN', 'Haxe', 'Racket', 'Logos'];
      (ref4 = this.availableSkills).push.apply(ref4, newSkills);
      'ngInject';
    }

    FindDevelopersCtrl.prototype.updateMap = function() {
      if ((typeof this.mapLocation) === 'object') {
        google.maps.event.trigger(this.$scope.map, 'resize');
        return this.$scope.map.setCenter(this.mapLocation.geometry.location);
      }
    };

    FindDevelopersCtrl.prototype.showMap = function() {
      return (typeof this.mapLocation) === 'object';
    };

    FindDevelopersCtrl.prototype.addMarker = function(location) {
      return new google.maps.Marker({
        position: location,
        map: this.$scope.map,
        draggable: false,
        animation: google.maps.Animation.DROP
      });
    };

    FindDevelopersCtrl.prototype.showDevelopersList = function() {
      return this.showDevelopersList;
    };

    FindDevelopersCtrl.prototype.listDevelopers = function() {
      return this.showDevelopersList = true;
    };

    FindDevelopersCtrl.prototype.devMe = function() {
      this.showDeveloperMessage = true;
      return this.$uibModal.open({
        animation: true,
        templateUrl: 'app/find_developers/find_developers.html'
      });
    };

    return FindDevelopersCtrl;

  })();

  angular.module('devme').controller('FindDevelopersController', ['$state', '$http', '$scope', '$uibModal', FindDevelopersCtrl]);

}).call(this);



